import React, { Component } from 'react';
import Title from './Header/Title';
import Navbar from './Header/Navbar';
import { Jumbotron } from 'react-bootstrap';

// COVER PHOTOS from: https://googleplus-covers.com/fantasy-covers
import homeIm from './Header/assets/home.jpg';
import thoughtsIm from './Header/assets/thoughts.jpg';
import travelsIm from './Header/assets/travels.jpg';
import aboutIm from './Header/assets/about.jpg';

class Header extends Component {
    constructor(props) {
	super(props);
	this.state = {
	    title: "kylerlittle.github.io",
	    path: "Home",
	    cover: homeIm
	}
    }

    nextImage(path) {
	switch (path) {
	case '/':
	    return homeIm;
	case '/thoughts':
	    console.log("matched thoughts");
	    return thoughtsIm;
	case '/travels':
	    return travelsIm;
	case '/about':
	    return aboutIm;
	default:
	    return homeIm;      // by default, display home image
	}
    }

    getPageTitle(path) {
	var re =/\/(.+)\/?/;    // match the first area path after a '/'
	var pageTitle = re.exec(path);
	// If no matches, return "Home"; else, return propercase version of match
	return pageTitle !== null ? pageTitle[1].charAt(0).toUpperCase() + pageTitle[1].slice(1) : "Home"
    }
    
    onRouteChange(newPath) {
	var nextIm = this.nextImage(newPath);
	this.setState({
	    title: this.state.title,
	    path: this.getPageTitle(newPath),
	    cover: nextIm
	});
    }

    render() {
	var jumbotronStyle = {
	    height: '350px',
	    marginBottom: '0px',
	    paddingTop: '5px',
	    paddingBottom: '0px',
	    background: `url(${this.state.cover})`,
	    backgroundSize: 'cover',
	    backgroundRepeat: 'no-repeat',
	    color: 'white',
	    textShadow: '#444 0 5px 5px'
	};
	return (
		<header>
		<Jumbotron style={jumbotronStyle}> 
		<Title title={this.state.title} path={this.state.path} />
		</Jumbotron>
		<Navbar onRouteChange={this.onRouteChange.bind(this)} />
		</header>
	);
    }
}

export default Header;



